import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"
import layoutStyles from "./layout.module.css"

const Cover = props => {
  const imageElement = (src) => {
    if (typeof src === 'string') {
      // Use regular image if it's a string
      return (<img src={src} alt="Cover"/>)
    }
    else {
      // You are passing a Gatsby Img object 
      return (<Img fluid={src} alt="Cover"/>);
    }
  }
  
  return (
    <div className={`${layoutStyles.cover} full-viewport-width`}>
      { props.src && imageElement(props.src)}
      { props.text && <h1 className="d-none d-sm-block">{props.text}</h1> }
    </div>
  )
};

Cover.defaultProps = {
  src: null,
  text: null
}

export default Cover;
